import { View, Text, TouchableWithoutFeedback } from "react-native";
import React from "react";
import {
    ButtonOuter,
    ButtonInner,
    ButtonFaceGradient,
} from "./styles";
import { useCallback, useState, useMemo } from "react";

type ButtonProps = {
    onPress: () => void;
    size: number;
    distance: number;
    blur: number;
    borderRadius: number;
}

function NeumorphismSimpleButton(props: ButtonProps) {
    const { onPress, size = 150, distance = 20, blur, borderRadius } = props;
    const [isDown, setDown] = useState(false);

    const handlePressIn = useCallback(() => {
        setDown(true);
    }, [setDown]);

    const handlePressOut = useCallback(() => {
        setDown(false);
    }, [setDown]);
    const gradientColors = useMemo(() => isDown ? ['#4da7db', '#5bc6ff'] : ['#5bc6ff', '#4da7db'], [isDown]);


    return (
        <TouchableWithoutFeedback
            style={{ width: size + distance, height: size + distance }}
            onPress={onPress}
            onPressIn={handlePressIn}
            onPressOut={handlePressOut}>
            <ButtonOuter blur={blur} distance={distance} size={size}>
                <ButtonInner blur={blur} distance={distance} size={size}>
                    <ButtonFaceGradient distance={distance} size={size} colors={gradientColors}  >

                    </ButtonFaceGradient>
                </ButtonInner>
            </ButtonOuter>
        </TouchableWithoutFeedback>
    );
}

NeumorphismSimpleButton.defaultProps = {

}

export default NeumorphismSimpleButton;
